var that;
var arr = [];
export default {
	data() {
		return {
			loading:'',//loading,more,noMore
			CustomBar: this.CustomBar,
			page: 1,
			list: [],
			keyword: '',
			nolist: false,
			local: [],
			localshow: true,
			modal: false,
			page_count:'',
			row_count:''
		};
	},
	methods: { 
		getlist(e) {
			let page = that.page;
			that.loading = true;
			that.$request
				.request({
					url: that.api.live.list, 
					data: { 
						keyword: that.keyword,
						page: page, 
					}
				})
				.then(res => {
					let list = res.list; 
					if (list.length > 0) {
						that.list = that.list.concat(list);
						that.page++; 
						that.nolist = false
					} else {
						that.nolist = true;
					} 
					console.log(res);
					uni.hideLoading();
					uni.stopPullDownRefresh();
					that.loading = false;
				});
		},
		del() {
			that.local = [];
			uni.setStorageSync('locallevel', that.local); 
		}, 
		//获取显示历史记录
		fo() {
			var local = uni.getStorageSync('locallevel');
			// 历史记录倒序
			if (local) {
				local.reverse();
				that.local = local;
				that.localshow = true;
			} else {
				uni.setStorageSync('locallevel', []);
			}
		},
		searchList(e) {
			//console.log(this.local[e])
			that.keyword = that.local[e];
			that.page = 1;
			that.list = [];
			that.getlist();
			that.nolist = false;
			// 隐藏历史记录
			that.localshow = false;
		},
		getsearch(e) {
			that.keyword = e.detail.value;
			that.nolist=false;
			if (that.keyword.length == 0){
				that.localshow = true;
				that.fo();
			}
		},
		search(e) {
			that.page = 1;
			that.list = [];
			that.nolist = false;
			that.getlist();  
			that.localshow = false;
			var val =that.keyword; 
			val = val?val.trim():val;
			if (val!= '') {
				if (uni.getStorageSync('locallevel') != []) {
					arr = uni.getStorageSync('locallevel');
					// 不重复添加值
					if (arr.indexOf(val) == -1) {
						arr.push(val);
					}
					//最多显示15条历史记录
					if (arr.length > 15) {
						arr.shift();
					}
				} else {
					// 不重复添加值
					if (arr.indexOf(val) == -1) {
						arr.push(val);
					}
				}
			}
			uni.setStorageSync('locallevel', arr);
			that.local=arr; 
		},
		tpinfo(e) {
			let id = e.currentTarget.dataset.id;
			let status = e.currentTarget.dataset.status;
			let url = '';
			status == 2 ? (url = '/pages/liveBroadcast/yugaoInfo/yugaoInfo?id=' + id) : (url = '/pages/liveBroadcast/livePLayer/player/player?id=' + id);
			uni.navigateTo({
				url: url
			});
		},
	},

	onLoad() {
		that = this;
		// 显示历史记录
		that.fo();
	},
	onReachBottom() {
		that.getlist();
	}
};
